import React, { useEffect, useState } from 'react'
import { IMAGE } from '../utils/theme'
import { NavLink, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'
import { GetStateList, GetSubject, Postmember, signupPost } from '../utils/Apirequest'
import TopBar from '../common/TopBar'
import Sidebar from '../common/Sidebar'
import Select from 'react-select';

const AddSme = () => {
const [subjectlist, setsubjectlist] = useState([])
const [selectsubject, setselectsubject] = useState([])
        const [fname, setfname] = useState("")
        const [lname, setlname] = useState("")
        const [email, setemail] = useState("")

        const [loading, setloading] = useState(false)
        const [pimage, setpimage] = useState(null)
        const [pfile, setpfile] = useState('')
        let navigate = useNavigate();

      

         const HandleImage = async (e) => {
            var file = e.target.files[0];
         
            var reader = new FileReader();
          
            reader.onloadend = async function (e) {
              const fsize = file.size;
        
              const fileSize = Math.round(fsize / 1024);
              if (fileSize >= 800) {
                toast.error('file size is too large');
              } else {
                setpfile(file)
                setpimage(reader.result)
        
        
              }
            };
            reader.readAsDataURL(file);
          };

        async function getApi() {
        
            let response = await GetSubject()
        
            if(response?.success){

                var TempArr =  []

                response?.data?.list?.forEach(element => {
                    TempArr.push({
                        id:element?.id,
                        label:element?.name,
                        value:element?.id
                    })
                });


                setsubjectlist(TempArr)
            }
            
        }


           useEffect(()=>{
            getApi()
           },[])
    
         async function SignupHandle(){

        let reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w\w+)+$/;
        if(fname == ""){
            toast.error('First name should be must');
        } else if(lname == ""){
            toast.error('Last name should be must');
        }  else if (reg.test(email) === false) {
             toast.error('Email should be proper!');
        }  else {
    
            var FormData = require('form-data');
            var data = new FormData();
         
            data.append("first_name", fname);
            data.append("last_name", lname);
            data.append("email", email);
            data.append("image", pfile);
            selectsubject?.forEach(elem =>{
                data.append("subjects[]", elem.id);
            })
    
            setloading(true)
    
            let response = await Postmember(data)
            setloading(false)
            console.log("response", response)
            if(response?.success){
            
                toast.success(response?.message)
    
                navigate("/sme-list");
            } else {
                toast.error(response?.error)
            }
        }
        

}

function SubjectHandle(item){
    console.log(item)
    setselectsubject(item)
}


   


  return (

    <div className='dashboard-panel'>
        <TopBar/>
        <div className='container-fluid'>
            <Sidebar/>
            <div className='right-comon'>
                <h3 className='page-title'>Add SME</h3>
                <div className='row mt-3'>
       
      <div className='col-lg-6'>
          <div className='form-group'>
              <label>First Name <span style={{color:'red'}}>*</span></label>
              <input type='text' className='form-control' placeholder='First Name'
                value={fname}
                onChange={(e)=>setfname(e.target.value)}
              />
          </div>
      </div>
      <div className='col-lg-6'>
          <div className='form-group'>
              <label>Last Name <span style={{color:'red'}}>*</span></label>
              <input type='text' className='form-control' placeholder='Last Name'
                    value={lname}
                    onChange={(e)=>setlname(e.target.value)}
              />
          </div>
      </div>
      <div className='col-lg-6'>
          <div className='form-group'>
              <label>Email Address <span style={{color:'red'}}>*</span></label>
              <input type='email' className='form-control' placeholder='Email' 
                    value={email}
                    onChange={(e)=>setemail(e.target.value)}
              />
          </div>
      </div>
   
    
            <div className='col-lg-6'>
                <div className='form-group'>
                    <label>Subject<span style={{color:'red'}}>*</span></label>
                    <Select
                
                    isMulti
                    name="label"
                    options={subjectlist}
                    className="basic-multi-select form-control"
                    onChange={SubjectHandle}
                />
                </div>
            </div>
     
     <div className='col-lg-12'>
      <div className='form-group'>
      <label>Upload Photo <span style={{color:'red'}}>*</span></label>
          <div className='row align-items-center'>
              <div className='col-lg-2'>
              {pimage !== null ?
                      <img src={pimage} alt="profile" width="150"/>
                      :
                      

                      <img src={IMAGE.default} alt="profile"  width="150" />
              
              }
              </div>
              <div className='col-lg-10'>
                  <div className='file-upload'>
                      <input type="file" accept="image/*" onChange={HandleImage} className='form-control' />
                      
                  </div>
              </div>
          </div>
          </div>
          
             </div>
  
             

      <div className='col-lg-12'>
      <button className='ThemeBttn' onClick={()=>SignupHandle()}>
          {loading ? 
          <>
           <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
           <span className="visually-hidden">Loading...</span>
          </>
          :
          'Add SME'
      }
            
          
      </button>
      </div>
   </div>
            </div>
        </div>
    </div>
     
  )
}

export default AddSme