import React, { useEffect, useState } from 'react'
import TopBar from '../common/TopBar'
import Sidebar from '../common/Sidebar'
import { NavLink } from 'react-router-dom'
import Loader from '../utils/Loader'
import { GetSubject } from '../utils/Apirequest'

const SubjectList = () => {
const [subjectlist, setsubjectlist] = useState([])




async function getApi() {

    let response = await GetSubject()

    if(response?.success){
        setsubjectlist(response?.data?.list)
    }
    
}

useEffect(()=>{
    getApi()
},[])




  return (
    <div className='dashboard-panel'>
      
    <TopBar/>
    <div className='container-fluid'>
        <Sidebar/>
        <div className='right-comon'>
             <div className='d-flex  mb-3 justify-content-between align-items-center'>
             <h3 className='page-title'>Subject List</h3>
                       <NavLink to="/add-subject" className='btn btn-primary'>Add Subject</NavLink>
                       </div>

                       {/* <Loader /> */}
           
            <table className='table custom-table'>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                        {subjectlist?.map((item, i)=>{
                            return (
                                <tr key={i}>
                                <td>{item?.name}</td>
                              
                                <td>
                                    <button className='btn btn-sm btn-outline-primary mr-2'>
                                         <i className="fa-solid fa-pencil"></i>
                                    </button>
                                    <button className='btn btn-sm btn-outline-danger'>
                                    <i className="fa-solid fa-trash"></i>
                                    </button>
                                </td>
                            </tr>
                            )
                        })}
                  
                   
                    </tbody>
                   
                </table>
        </div>
    </div>
</div>
  )
}

export default SubjectList